.nav-menu{
    display: grid;
    grid-template-columns: repeat(5,auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    -webkit-justify-content: end;
            justify-content: end;
    margin-right: 2rem;
}
.nav-links{
    position: relative;
    color:rgb(5, 5, 5);
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.NavbarItems .nav-links:hover{
    /*color: rgb(21,156,180,1);
    box-shadow: 0px 2px rgb(21,156,180,1);
    transition: all 0.2s ease-out; */
    color: #28b5b5;
    transition: .9s;
}

.NavbarItems .nav-links:after{
    content: "";
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%) scaleX(0);
    -webkit-transform: translateX(-50%) scaleX(0);
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    width: 100%;
    height: 2.5px;
    background-color: #28b5b5;
    transition: 500ms;
}
.NavbarItems .nav-links:hover:after{
-webkit-transform: translateX(-50%) scaleX(1);
    transform: translateX(-50%) scaleX(1);
}
.NavbarItems .nav-links.activated{
    color: #28b5b5;
}




.NavbarItems.active .nav-links:hover{
    color: rgb(5, 5, 5);
    background-color:  rgb(144, 214, 226);
    border-radius: 0;  
    transition: all 0.2s ease-out; 
}
.fa-bars{
    color: rgb(5, 5, 5);
}
.nav-links-mobile{
    display: none;
}
.menu-icon{
    display: none;
}
@media screen and (max-width: 960px){
    .NavbarItems{
        position: relative;
    }
    .nav-menu{
        display:-webkit-flex;
        display:flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        width: 100%;
        height: 500px;
        position: absolute;
        top: 80px;
        left:-100%;
        opacity: 1;
        transition: all 0.5s ease;
    }
    .nav-menu.active{
        background:   rgb(21,156,180,1);
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }
    .nav-links{
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }
   .nav-links:hover{
       background-color:  rgb(144, 214, 226);
        border-radius: 0;   
    }
    .navbar-logo{
        position: absolute;
        top: 0;
        left: 0;
        -webkit-transform:translate(-100%,50%);
                transform:translate(-100%,50%);
    }
    .menu-icon{
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        -webkit-transform:translate(-100%,60%);
                transform:translate(-100%,60%);
        font-size: 1.8rem;
        cursor: pointer;
    }
}
