outline:none;border-bottom:2px solid  #05f7ff}#contact-container form .form-group:nth-child(2){padding-right:0}#contact-container form .form-group.solo{padding:0}#contact-container form .form-group.right{-webkit-align-items:flex-end;align-items:flex-end}#contact-container form .form-group button{background:transparent;position:absolute;cursor:pointer;left:88%;top:85%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);color:#05f7ff;text-decoration:none;font-size:1.5em;display:inline-block;font-family:Montserrat;text-transform:uppercase;padding:0.5em 2em;border:2px solid #05f7ff;transition:0.02s 0.2s cubic-bezier(0.1, 0, 0.1, 1)}#contact-container form .form-group button::before{content:"";display:inline-block;position:absolute;top:0;left:0;right:100%;bottom:0;background:#05f7ff;transition:0.3s 0.2s cubic-bezier(0.1, 0, 0.1, 1),left 0.3s cubic-bezier(0.1, 0, 0.1, 1);z-index:-1}#contact-container form .form-group button::after{content:"";display:inline-block;background-image:url("https://image.flaticon.com/icons/png/128/109/109617.png");position:absolute;top:0;left:calc(100% - 3em);right:3em;bottom:0;background-size:1.5em;background-repeat:no-repeat;background-position:center;transition:right 0.3s cubic-bezier(0.1, 0, 0.1, 1)}#contact-container form .form-group button:hover{padding:0.5em 3.5em 0.5em 0.5em}#contact-container form .form-group button:hover::before{left:calc(100% - 3em);right:0;transition:0.3s cubic-bezier(0.1, 0, 0.1, 1),left 0.3s 0.2s cubic-bezier(0.1, 0, 0.1, 1)}#contact-container form .form-group button:hover::after{right:0;transition:right 0.3s 0.2s cubic-bezier(0.1, 0, 0.1, 1)}

.loader{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 9999;
    width: 100%;
    height: 100%;
    background-color: white;
    -webkit-animation: anim2 1s cubic-bezier(0.19,1,0.22,1) 7s both;
            animation: anim2 1s cubic-bezier(0.19,1,0.22,1) 7s both;
  }
  .wrapperload{
    width: 600px;
    height: 100px;
    overflow: hidden;
   /*  background-color: #dddddd;*/
  }
  .wrapperload .elem{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height:100%;
  /*background-color: #c8c6a7;*/
  }

  .elem h3{
    color: #000000;
  }
  .wrapperload .elem:nth-child(1)
  {
    -webkit-animation: anim 1.5s cubic-bezier(0.19,1,0.22,1) 1.5s both;
            animation: anim 1.5s cubic-bezier(0.19,1,0.22,1) 1.5s both;
  }
  .wrapperload .elem:nth-child(2)
  {
    -webkit-animation: anim 1.5s cubic-bezier(0.19,1,0.22,1) 3s both;
            animation: anim 1.5s cubic-bezier(0.19,1,0.22,1) 3s both;
  }
  .wrapperload .elem:nth-child(3)
  {
    -webkit-animation: anim 1.5s cubic-bezier(0.19,1,0.22,1) 4.5s both;
            animation: anim 1.5s cubic-bezier(0.19,1,0.22,1) 4.5s both;
  }
  .wrapperload .elem img{
    width: 50%;
  }
  @-webkit-keyframes anim{
    to{
      margin-top: -100px;
    }
  }
  @keyframes anim{
    to{
      margin-top: -100px;
    }
  }
  @-webkit-keyframes anim2{
    to{
      -webkit-transform:translateY(-100%);
              transform:translateY(-100%);
    }
  }
  @keyframes anim2{
    to{
      -webkit-transform:translateY(-100%);
              transform:translateY(-100%);
    }
  }
